import Button from "@material-ui/core/Button";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select";
import React, { Component } from "react";
import { withRouter } from "react-router";
import Swal from "sweetalert2";
import AuthService from "../../AuthService";
import PageLoading from "../pageLoading";
import "./style.css";

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showLoadding: false,
            fullName: "",
        };
        this.Auth = new AuthService();
    }

    componentDidMount() {
        const fullName = localStorage.getItem("fullName");
        if (fullName) {
            const { history } = this.props;
            history.replace("/schedule/view");
        }
    }

    onChangeInput = (e) => {
        this.setState({
            ...this.state,
            fullName: e.target.value,
        });
    };

    onSubmitFormLogin = async () => {
        const { history } = this.props;
        const { fullName } = this.state;
        if (!fullName) {
            Swal.fire(
                "Vui lòng nhập tên của bạn !",
                "vd: dangtruonggiang",
                "error"
            );
        } else if (
            typeof fullName === "undefined" ||
            fullName.trim().length < fullName.length ||
            fullName.split(" ").length > 1 ||
            fullName === null ||
            !fullName.match(/^[0-9a-zA-Z]{0,}$/)
        ) {
            Swal.fire(
                "Tài khoản hoặc Mật khẩu của bạn không đúng định dạng !",
                "",
                "error"
            );
        } else {
            this.showLoadding();
            await this.Auth.fetchDataSchedulesApi(fullName).then((res) => {
                if (res.status === 200) {
                    localStorage.setItem("fullName", fullName);
                    setTimeout(() => {
                        this.showAlert(true);
                        this.hideLoadding();
                        history.replace("/schedule/view");
                    }, 1500);
                } else {
                    this.showAlert(false);
                    setTimeout(() => {
                        this.hideLoadding();
                    }, 1500);
                }
            });
        }
    };
    showAlert = (status) => {
        const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            onOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
            },
        });
        Toast.fire({
            icon: `${status ? "success" : "error"}`,
            title: `${
                status
                    ? "Đăng nhập thành công !"
                    : "Đã xảy ra lỗi ! Hãy kiểm tra lại !"
            }`,
        });
    };
    showLoadding = () => {
        this.setState({
            showLoadding: true,
        });
    };
    hideLoadding = () => {
        this.setState({
            showLoadding: false,
        });
    };
    render() {
        const { showLoadding, fullName } = this.state;
        return (
            <div className="container">
                {showLoadding ? (
                    <div className="loadding">
                        <PageLoading />
                    </div>
                ) : (
                    ""
                )}
                <div className="background">
                    <div className="login">
                        <h1>Thời Khóa biểu KMA</h1>
                        <form className="login-form">
                            <div className="login-form-account">
                                <FormControl variant="outlined" className="">
                                    <InputLabel id="demo-simple-select-outlined-label">
                                        Họ và Tên
                                    </InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        value={fullName}
                                        onChange={this.onChangeInput}
                                        label="Họ và tên"
                                    >
                                        <MenuItem value="dangtruonggiang">
                                            Đặng Trường Giang
                                        </MenuItem>
                                        <MenuItem value="letranminhhieu">
                                            Lê Trần Minh Hiếu
                                        </MenuItem>
                                        <MenuItem value="nguyenvanlong">
                                            Nguyễn Văn Long
                                        </MenuItem>
                                    </Select>
                                </FormControl>
                            </div>
                            <Button
                                onClick={this.onSubmitFormLogin}
                                variant="contained"
                                color="primary"
                            >
                                GO
                            </Button>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}
export default withRouter(Login);
