import React from 'react';
import "./style.css";

const PageLoading = () => {
    return (
      <div className="container-page-loading">
        <span className="custom-page-loading">
          <span>{`4`}</span>
          <span>{`0`}</span>
          <span>{`5`}</span>
        </span>
      </div>
    );
  };
  
  export default PageLoading;