import axios from 'axios';

export default class AuthService {
    constructor(domain) {
        this.domain =
            domain || 'https://5f3a9d2f2300b100169a8efe.mockapi.io';
    }

    fetchDataSchedulesApi = (fullName) => {
        const headers = {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        };
        const requestOptions = {
            header: headers,
            method: 'GET',
            redirect: 'follow',
        };

        return axios(`${this.domain}/${fullName}`, requestOptions).then((res) => {
            return Promise.resolve(res);
        });
    };
}
